* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.buttons-box button {
  background-color: #b7d2d7;
  border: 2px solid #000;
  padding: 15px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 30px;
  font-weight: 600;
}

button:hover {
  scale: 1.05;
}

.buttons-box {
  justify-content: center;
  gap: 30px;
  display: flex;
}

#datetime-picker, .start-timer-btn {
  border: 2px solid #0c5a7b;
  margin: 0 auto;
  padding: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 25px;
  display: block;
}

.start-timer-btn {
  background-color: #b7d2d7;
  border: 2px solid #0c5a7b;
  margin-top: 15px;
}

.timer {
  justify-content: center;
  gap: 50px;
  margin-top: 50px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 25px;
  display: flex;
}

.value {
  font-size: 50px;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

